.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
  }
  .header__logo {
    height: 22px;
    object-fit: contain;
    margin-left: 22px;
    padding: 2px;
  }
  .header__input {
    display: flex;
    align-items: center;
    width: 40%;
    border: 1.5px solid black;
  }
  
  .header__inputButton {
    width: 50px !important; /* overwrite material-ui */
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
    cursor: pointer;
  }
  
  .header__icons {
    display: flex;
    align-items: center;
  }
  .header__icon {
    margin-right: 10px;
    cursor: pointer;
  }
  
  .header__input > input {
    flex: 1;
    border: none;
    outline: none;
    font-size: 1rem;
  }